* {
  padding: 0;
}

.prev_button {
  position: absolute;
  border: none;
  background: none;
  background-image: url(./slider_left.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  left: 0;
  border: none;
  height: 100%;
  width: 64px;
  align-items: center;
}

.prev_button:hover {
  background: rgba(14, 194, 97, 0.2);
  background-image: url(./slider_left.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}

.next_button {
  position: absolute;
  right: 0;
  border: none;
  background: none;
  background-image: url(./slider_right.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  height: 100%;
  width: 64px;
  align-items: center;
}

.next_button:hover {
  background: rgba(14, 194, 97, 0.2);
  background-image: url(./slider_right.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}

.carousel .carousel-indicators {
  margin-bottom: 32px;
}

.carousel .carousel-indicators button {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #EEEEEE;
  margin-right: 8px;
}

.carousel .carousel-indicators button.active {
  background-color: #0EC261;
}

.slide_1 {
  height: 100vh;
  background: none;
  background-image: url('./slide_1_pic.png');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: row;
}

